body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.clickItem {
  background-color: grey;
  height: 300px;
  width: 300px;
}

.card {
  border: 4px solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
}

.img-container {
  height: 100%;
  overflow: hidden;
  background: #ffffff;
}

img {
  height: 100%;
}

.header-text {
  color: black;
  margin: auto;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

